.__container-screen__952d4{@import "./themes/default";
@import "./mixins/flex";

& {
	height: 100vh;
	position: relative;
	display: flex;
	flex-wrap: nowrap;
	flex-direction: column;

	@keyframes blur {
		from {
			-webkit-filter: blur(0);
		}
		to {
			-webkit-filter: blur(2px);
		}
	}

	content {
		flex: 1;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
		background: $color-bg-default;

		&.padded {
			padding: $unit-1x;
		}

		&::-webkit-scrollbar {
			display: none;
		}

	}

	.footer {
		@include flexbox;
		background: $color-bg-default;
		border-top: 0.5px solid #dddddd;
		height: $height-footer;
		text-align: center;
	}
}
}.__item-post__59210{@import "./themes/default";
@import "./mixins/flex";

& {

  .header {
    @include flexbox;
    @include align-items(center);
    padding: $unit-1x;
    border-top: $unit-1x solid #d5d6db;

    .description {
      @include flex(1);

      .title {
        color: $color-text;
        font-weight: bold;
        font-size: $font-size-base;
      }

      .sub-title {
        color: $color-text-muted;
        font-size: $font-size-small;
      }
    }


    img {
      width: 35px;
      height: 35px;
      margin-right: $unit-1x;
    }

    button {
      border: 1px solid #3da0fe;
      background: none;
      border-radius: 3px;
      border-width: 0.5;
      padding: 7px 25px 6px 25px;
      font-size: 10px;
      color: #3da0fe;
      letter-spacing: 0.4px;
      font-weight: bold;
      text-transform: uppercase;
    }

  }

  .actions {
    @include flexbox;
    text-align: center;
    background-color: #fafafa;
    border-top: 0.5px solid #dddddd;

    padding: $unit-1x 0;
    font-size: $font-size-small;
    letter-spacing: 0.4px;
    color: #565b5f;

    button {
      @include flex(1);
      color: #9197a3;
      background: none;
      font-size: $font-size-small;
      border: none;
      letter-spacing: 0.4px;
      font-weight: bold;
    }
  }

}
}.__item-review__3a27f{@import "./themes/default";
@import "./mixins/flex";

& {
  width: calc(100vw - #{$unit-3x});

  margin-left: 6px;
  margin-bottom: $unit-2x;

  &:last-child {
    margin-right: $unit-1x;
  }

  &:first-child {
    margin-left: $unit-1x;
  }

  -webkit-box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2);
  -moz-box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2);
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2);

  img {
    display: block;
    border-bottom: 0.5px solid #dddddd;
    height: 225px;
  }

  .details {
    @include flexbox;
    padding: $unit-1x $unit-1x $unit-2x;

    .reaction {
      width: $unit-2x;
      height: $unit-2x;
      border-radius: 50%;
      background-color: #5890ff;
      display: inline-block;
      margin-right: $unit-1x;

      &.liked {
        background-color: #5890ff;
      }
      &.disliked {
        background-color: #f25268;
      }
      &.favourited {
        background-color: #ffd972;
      }
    }

    p {
      @include flex(1);
      line-height: 18px;
      margin: 0;
      white-space: normal;

      .author {
        font-size: $font-size-small;
        color: $color-text-muted;
        margin-bottom: 3px;
      }
    }
  }

}
}.__navigation__footer-main__33634{@import "./mixins/flex";
@import "./themes/default";

& {
  @include flexbox;
  @include align-items(stretch);
  height: $height-footer;
  background: $color-bg-default;
  border-top: 0.5px solid #dddddd;
  text-align: center;
}
}.__start-review__84a87{@import "./mixins/flex";
@import "./themes/default";

& {
  @include flexbox;
  @include flex-direction(column);

  background-color: $color-bg-default;
  height: 235px;
  width: 100vw;

  .message {
    @include flex(1);
    text-align: center;
    padding: $unit-3x $unit-1x;
  }

  .places {
    @include flex(1);
    padding: $unit-2x;

    .place {
      text-align: center;
      display: inline-block;
      width: 100px;
      vertical-align: top;
      img {
        display: block;
        border-radius: 50%;
        width: 75px;
        height: 75px;
        margin: 0 auto $unit-1x;
        border: $border-thin;
      }
      small {
        display: block;
        overflow: hidden;
        width: 100px;
        font-size: $font-size-xsmall;
        text-overflow: ellipsis;
        text-align: center;
      }
    }
  }

  .search {
    padding: $unit-1x;
    border-bottom: 0.5px solid #dddddd;
    input {
      width: 100%;
      box-sizing: border-box;
      border: none;
      font-size: 14px;
      padding: 6px 9px;
      background-color: rgba(0, 0, 0, 0.1);
      border-radius: 3px;
      text-align: center;
      color: #1c1d22;
    }
  }

  .cancel {

  }

}
}